import { useEffect, useState } from 'react';
import "./index.css"
import { validation } from './validation'
import Axios from "../../api/Axios";
import { login } from '../../store/user';
import { Link, useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import triggerSwalAlert from '../../helpers/triggerSwalAlert';
import Loader from '../../components/Loader';
import jwt_decode from 'jwt-decode'


const axios = new Axios();

function SignUp() {

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [pending,setPending] = useState(false)
  const google = window.google;


  const [error, setError] = useState({
    firstName: false,
    lastName: false,
    password: false,
    email: false,
    // username: false,
    phoneNumber: false,
    message: ""
  });

  const [data, setData] = useState({
    firstName: "",
    lastName: "",
    // username: "",
    phoneNumber: "",
    email: "",
    password: ""
  });

  // const [isUsernameTaken, setIsUsernameTaken] = useState(false)

  const onChange = (value, target) => {
    setData({
      ...data,
      [target]: value,
    });
    if (error.email === true || error.password === true || error.firstName === true || error.lastName === true || error.phoneNumber === true) {
      setError({
        ...error,
        [target]: false
      })
    }
  };


  // const checkUsername = async () => {
  //   const options = {
  //     method: "GET",
  //   }
  //   if (data.username) {
  //     const result = await axios.callApi(`auth/checkByUsername/${data.username}`, options)
  //     if (result.data.usernameExists) {
  //       setError({ ...error, username: true })
  //       setIsUsernameTaken(true)
  //     }
  //     else {
  //       setError({ ...error, username: false })
  //       setIsUsernameTaken(false)
  //     }
  //   }
  //   else {
  //     setError({ ...error, username: false })
  //     setIsUsernameTaken(false)
  //   }

  // }

  const handleSubmit = async (e) => {
    try {
      e.preventDefault()
      const valid = validation(data)
      // data.username && !isUsernameTaken ? setError(valid) : setError({ ...valid, username: true })
      setError(valid)
      if (valid.phoneNumber || valid.firstName || valid.lastName || valid.email || valid.password) {
        return
      }
      const options = {
        method: "POST",
        body: data
      }
      setPending(true)
      const result = await axios.callApi(`auth/createUser`, options)

      if (result.status == 200) {
        setPending(false)
        dispatch(login({
          userId: result.data.id,
          accessToken: result.data.accessToken,
          // username: result.data.username,
          role: result.data.role,
          firstName: result.data.firstName,
          lastName: result.data.lastName,
          email: result.data.email,
          contact: result.data.phoneNumber
        }))

        navigate("/")
      }
      else{
        setPending(false)
        triggerSwalAlert("Error", result.data.errorMessage || "Something went wrong", "error")
      }

    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  const handleGoogleSignIn = async (res) => {
    const googleSignInCredential = jwt_decode(res.credential)

    const options = {
      method: "POST",
      body: googleSignInCredential
    }
    try {
      setPending(true)
      const result = await axios.callApi(`auth/loginWithGoogle`, options)

      if (result.status == 200) {
        setPending(false)
        dispatch(login({
          userId: result.data.id,
          accessToken: result.data.accessToken,
          role: result.data.role,
          firstName: result.data.firstName,
          lastName: result.data.lastName,
          email: result.data.email,
          contact: result.data.phoneNumber
        }))

      }
      else {
        setPending(false)
        triggerSwalAlert("Error", result.data.errorMessage || "Something went wrong", "error")
      }
    }
    catch (error) {
      console.error("Something went wrong")
    }
  }

  useEffect(() => {

    google.accounts.id.initialize({
      client_id: process.env.REACT_APP_GOOGLE_CLIENT_ID,
      callback: handleGoogleSignIn
    });

    google.accounts.id.renderButton(
      document.getElementById("SignInWithGoogle"),
      { theme: "outline", size: 'large' }
    )
  }, [])

  // useEffect(() => {
  //   const typingTimer = setTimeout(() => {
  //     checkUsername(data.username)
  //   }, 1000);

  //   return () => {
  //     clearTimeout(typingTimer)
  //   }
  // }, [data.username])


  return (
    <>
    {pending ? <Loader/> : null}
    <div className='signUp-wrapper bg-dark d-flex align-item-center justify-content-center w-100'>
      <div className='signUp'>
        <h2 className='mb-3'>Sign Up</h2>
        <form>
          <div className='form-group mb-2'>
            <label htmlFor="firstName" className='form-label'>First Name</label>
            <input type="text" id='firstName' className={`form-control ${error.firstName && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'firstName')} />
            {error.firstName && <div className='error'>
              Please enter your first name
            </div>}
          </div>
          <div className='form-group mb-2'>
            <label htmlFor="lastName" className='form-label'>Last Name</label>
            <input type="text" id='lastName' className={`form-control ${error.lastName && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'lastName')} />
            {error.lastName && <div className='error'>
              Please enter your last name
            </div>}
          </div>
          {/* <div className='form-group mb-2'>
            <label htmlFor="username" className='form-label'>Username</label>
            <input type="text" id='username' className={`form-control ${error.username && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'username')} />
            {error.username && <div className='error'>
              {isUsernameTaken ? "Username already use" : "Please enter your username"}
            </div>}
          </div> */}
          <div className='form-group mb-2'>
            <label htmlFor="email" className='form-label'>Email Address</label>
            <input type="email" id='email' className={`form-control ${error.email && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'email')} />
            {error.email && <div className='error'>
              {error.message ? "Please enter a valid email" : "Please enter your email"}
            </div>}
          </div>
          <div className='form-group mb-2'>
            <label htmlFor="phoneNumber" className='form-label'>Phone Number</label>
            <input type="number" id='phoneNumber' className={`form-control ${error.phoneNumber && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'phoneNumber')} />
            {error.phoneNumber && <div className='error'>
              Please enter valid phoneNumber number
            </div>}
          </div>
          <div className='form-group mb-2'>
            <label htmlFor="password" className='form-label'>Password</label>
            <input type="password" id='password' className={`form-control ${error.password && "errorInputField"}`} required onChange={e => onChange(e.target.value, 'password')} />
            {error.password && <div className='error'>
              Please enter your password
            </div>}
          </div>
          <button className='btn btn-dark w-100 mt-2' onClick={e => handleSubmit(e)}>SIGN UP</button>
          <div className="text-center mt-2">
            <p>Already a member? <Link to={"/sign-in"}>Sign In</Link></p>
          </div>
          <span style={{ display: 'block', textAlign: "center", margin:'0.7rem 0' }}> OR  </span>
          <div id="SignInWithGoogle" style={{display:'flex',justifyContent:"center"}}></div>
        </form>
      </div>
    </div>
    </>
  );
}

export default SignUp;