export const validation = (data) => {
  const { firstName, lastName, email, phoneNumber, password } = data;
  var errorVal = {
    firstName: false,
    lastName: false,
    password: false,
    email: false,
    phoneNumber: false,
    message: ""
  }

  if (!firstName) {
    errorVal["firstName"] = true
  }
  if (!lastName) {
    errorVal["lastName"] = true
  }
  if (!email) {
    errorVal["email"] = true
  }
  if (email && !(/\S+@\S+\.\S+/.test(email))) {
    errorVal["email"] = true
    errorVal["message"] = "invalid email"
  }
  if (!phoneNumber) {
    errorVal["phoneNumber"] = true
  }
  if (phoneNumber && phoneNumber.length != 10) {
    errorVal["phoneNumber"] = true
  }
  if (!password) {
    errorVal["password"] = true
  }
  return errorVal
};